@import url(https://fonts.googleapis.com/css2?family=Noto+Sans:wght@400;700&display=swap);
/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
    display: block;
}
body {
    line-height: 1;
}
ol, ul {
    list-style: none;
}
blockquote, q {
    quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
    content: '';
    content: none;
}
table {
    border-collapse: collapse;
    border-spacing: 0;
}
body{font-family:"Noto Sans",sans-serif}.main__container{width:100%;margin:0 auto;text-align:center;padding:3rem 0 5rem 0}.main__title{font-weight:bold;font-size:3rem;padding:2rem}.main__list{width:60vw;display:grid;grid-template-columns:1fr 1fr 1fr 1fr;margin:0 auto}.main__breed{list-style:none;padding:.8rem;font-size:1.5rem;border:1px solid #add8e6;margin:.5rem;cursor:pointer}.main__breed:hover{background:#add8e6;color:#fff;font-weight:700}.main input,.main label{cursor:pointer}.loader__container{position:fixed;top:0;left:0;z-index:5;width:100vw;height:100vh;background:rgba(255,255,255,.8);display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.loader__contents{position:relative;width:auto;height:auto;z-index:10}.loader__contents div{height:10px;width:10px;background-color:#add8e6;border-radius:50%;position:absolute;-webkit-animation:.8s opaque ease-in-out infinite both;animation:.8s opaque ease-in-out infinite both}.loader__contents>div:nth-child(1){top:-25px;left:0}.loader__contents>div:nth-child(2){top:-17px;left:17px;-webkit-animation-delay:.1s;animation-delay:.1s}.loader__contents>div:nth-child(3){top:0;left:25px;-webkit-animation-delay:.2s;animation-delay:.2s}.loader__contents>div:nth-child(4){top:17px;left:17px;-webkit-animation-delay:.3s;animation-delay:.3s}.loader__contents>div:nth-child(5){top:25px;left:0;-webkit-animation-delay:.4s;animation-delay:.4s}.loader__contents>div:nth-child(6){top:17px;left:-17px;-webkit-animation-delay:.5s;animation-delay:.5s}.loader__contents>div:nth-child(7){top:0;left:-25px;-webkit-animation-delay:.6s;animation-delay:.6s}.loader__contents>div:nth-child(8){top:-17px;left:-17px;-webkit-animation-delay:.7s;animation-delay:.7s}@-webkit-keyframes opaque{0%{opacity:.1}40%{opacity:1}80%{opacity:.1}100%{opacity:.1}}@keyframes opaque{0%{opacity:.1}40%{opacity:1}80%{opacity:.1}100%{opacity:.1}}
.modal__container{position:fixed;top:0;left:0;z-index:5;width:100vw;height:100vh;background:rgba(255,255,255,.8);display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.modal__contents{min-width:400px;min-height:200px;max-height:80vh;background-color:beige;padding:20px;overflow-y:scroll;margin:0 auto;text-align:center}.modal__contents.hidden{display:none}.modal__contents h1{font-size:30px;font-weight:700;margin:10px 0}.modal__contents h2{text-align:center;margin-top:30px;cursor:pointer}.modal__contents h2:hover{text-decoration:underline}.modal__contents p{font-size:25px;margin-top:25px}.modal__contents img{width:100%;height:auto;text-align:center;max-width:250px;max-height:300px}.modal__add{display:-webkit-flex;display:flex;-webkit-justify-content:flex-end;justify-content:flex-end;-webkit-align-items:center;align-items:center;margin-bottom:15px}.modal__add span{cursor:pointer}.modal__add span:nth-child(2):hover{text-decoration:underline}.modal__sub h1{font-size:20px}.heart{position:relative;width:20px;cursor:pointer;margin-right:2px}.heart.red{fill:#ce1212}.heart.red:hover{-webkit-animation:pulse 1s ease infinite;animation:pulse 1s ease infinite}@-webkit-keyframes pulse{0%{-webkit-transform:scale(1);transform:scale(1)}50%{-webkit-transform:scale(1.2);transform:scale(1.2)}100%{-webkit-transform:scale(1);transform:scale(1)}}@keyframes pulse{0%{-webkit-transform:scale(1);transform:scale(1)}50%{-webkit-transform:scale(1.2);transform:scale(1.2)}100%{-webkit-transform:scale(1);transform:scale(1)}}.heart.gray{fill:#bdc7c9}
