.modal {
  &__container {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 5;
    width: 100vw;
    height: 100vh;
    background: rgba(255, 255, 255, 0.8);
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__contents {
    min-width: 400px;
    min-height: 200px;
    max-height: 80vh;
    background-color: beige;
    padding: 20px;
    overflow-y: scroll;
    margin: 0 auto;
    text-align: center;
    &.hidden {
      display: none;
    }

    h1 {
      font-size: 30px;
      font-weight: 700;
      margin: 10px 0;
    }

    h2 {
      text-align: center;
      margin-top: 30px;
      cursor: pointer;

      &:hover {
        text-decoration: underline;
      }
    }

    p {
      font-size: 25px;
      margin-top: 25px;
    }

    img {
      width: 100%;
      height: auto;
      text-align: center;

      max-width: 250px;
      max-height: 300px;
    }
  }

  &__add {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-bottom: 15px;

    span {
      cursor: pointer;

      &:nth-child(2):hover {
        text-decoration: underline;
      }
    }
  }
  &__sub {
    h1 {
      font-size: 20px;
    }
  }
}


.heart {
  position: relative;
  width: 20px;
  cursor: pointer;
  margin-right: 2px;

  &.red {
    fill : #ce1212;
    &:hover {
      animation: pulse 1s ease infinite;
    }

    @keyframes pulse {
      0% { transform: scale(1); }
      50% { transform: scale(1.2); }
      100% { transform: scale(1); }
    }

  }

  &.gray {
    fill : #bdc7c9;
  }
}
